.wpcf7-form label {
  font-size: .6rem;
  font-weight: 300;
  color: gray;
  margin-bottom: 0;
}

.wpcf7-submit-wrapper {
  margin: 0;
}

.wpcf7-submit-wrapper p {
  position: relative;
  margin: 0;
}

.wpcf7-submit-wrapper .wpcf7-spinner {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: -12px;
}

#carouselNewFilms {
  /* medium and up screens */
}

#carouselNewFilms.carousel {
  padding: 0;
}

#carouselNewFilms.carousel .carousel-inner {
  padding-bottom: .5rem;
}

#carouselNewFilms.carousel .carousel-inner .card {
  margin: 0 .3em;
  box-shadow: 2px 6px 8px 0 rgba(22, 22, 26, 0.18);
  border: none;
}

#carouselNewFilms.carousel .carousel-inner .card-body {
  padding: 1em;
  font-size: .5em;
  text-align: left;
  line-height: 1.4em;
  position: relative;
  max-height: 200px;
  overflow: hidden;
}

#carouselNewFilms.carousel .carousel-inner .card-body:after {
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  width: 100%;
  background: linear-gradient(transparent, white);
  position: absolute;
  display: block;
  content: '';
}

#carouselNewFilms.carousel .carousel-inner .card-title {
  font-size: 1.1em;
  margin-bottom: .5em;
}

#carouselNewFilms.carousel .carousel-inner .carousel-item.active {
  display: flex;
}

#carouselNewFilms.carousel .carousel-inner .carousel-item-next {
  display: flex;
}

#carouselNewFilms.carousel .carousel-inner .carousel-item-prev {
  display: flex;
}

#carouselNewFilms.carousel .carousel-inner .carousel-item-end {
  transform: translateX(0);
}

#carouselNewFilms.carousel .carousel-inner .carousel-item-start {
  transform: translateX(0);
}

@media (max-width: 767px) {
  #carouselNewFilms .carousel-inner .carousel-item > div {
    display: none;
  }
  #carouselNewFilms .carousel-inner .carousel-item > div:first-child {
    display: block;
  }
}

@media (min-width: 768px) {
  #carouselNewFilms .carousel-inner .carousel-item-end.active {
    transform: translateX(25%);
  }
  #carouselNewFilms .carousel-inner .carousel-item-next {
    transform: translateX(25%);
  }
  #carouselNewFilms .carousel-inner .carousel-item-start.active {
    transform: translateX(-25%);
  }
  #carouselNewFilms .carousel-inner .carousel-item-prev {
    transform: translateX(-25%);
  }
}

.section-heading {
  text-align: center;
  font-size: 1.5em;
  margin-bottom: 1em;
}

.latest-news--list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.latest-news--item {
  font-size: .7em;
  line-height: 1.4em;
}

.latest-news--item .card-title {
  font-size: 1.3em;
}

#carouselReviews .card-body {
  font-size: .7em;
}

#carouselReviews .card-title {
  font-size: 1.3em;
}

#carouselReviews .glsr-bootstrap .glsr-review-date {
  flex: 0;
}

#carouselReviews .glsr-bootstrap .glsr-review-avatar img {
  margin: 0 auto;
}

#carouselReviews .author {
  color: #ccc;
  font-weight: 300;
}

#latestNewsSection .latest-news--link {
  text-decoration: none;
}
